@import '../../styles/variables.scss';

.intro {
    background-image: url('./img/intro-background.jpg');
    background-size: cover;
    background-position: center;
}
.intro__inner {
    padding-top: 215px;
}

.intro__title {
    max-width: 40%;
    font-size: 57px;
}
.intro__subtitle {
    max-width: 30%;
    margin-top: 25px;
}

#intro__read-more {
    margin-top: 30px;
}

.intro__findroom {
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 25px 50px;
    border-radius: 10px;
    -webkit-backdrop-filter: blur(70px);
            backdrop-filter: blur(70px);
    position: relative;
    bottom: -60px;
    left: -35px;
}
.intro__findroom-section {
    padding: 0 30px;
    position: relative;

    &::after {
        content: '';
        width: 2px;
        height: 34px;
        background-color: #d6dbdf;
        transform: translateY(-50%);
        position: absolute;
        top: 50%;
        right: -1px;
        z-index: 1;
    }

    &:first-child {
        padding-left: 0;
    }

    &:nth-child(3) {
        margin-right: 60px;
        padding-right: 0;

        &::after {
            display: none;
        }
    }
}
.intro__findroom-section-title {
    display: block;
    margin-bottom: 10px;
    color: $black;
    font-size: 16px;
    font-weight: 500;
    line-height: 160%;
}
.intro__findroom-section-label {
    color: $gray;
    line-height: 160%;
    cursor: pointer;
}
.intro__findroom-section-date {
    opacity: 0;
    visibility: none;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
}

.intro__findroom-person {
    display: flex;
}
.intro__select {
    color: $gray;
    background-color: transparent;
    line-height: 160%;
    cursor: pointer;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    padding: 0;

    &:first-child {
        margin-right: 5px;
    }
}
